
:root{
  --bg: rgb(131, 126, 118);
}

.Content{
  display: flex;
  flex-direction: column;
  justify-items: space-around;
  align-items: center;
  margin: 5% 0;
}

.Content1{
  width: 100%;
  height:80vh;
  margin: 20px;
  justify-content: center;
  display: flex;
  flex-direction: row;
}
@keyframes clicked {
  from{background-color: var(--bg)}
  to{background-color: rgb(177, 115, 44);}
}
.tiles,.tilesClicked{
  display: flex;
  flex-direction: column;
  width: 20%;
  margin: 5px;
  min-height: 300px;
  text-align: center;
  align-items: center;
  align-content: center;
  justify-content: center;
  justify-items: center;
}
.tiles{
  background-color: rgb(131, 126, 118);

}
.tilesClicked{
  background-color: rgb(177, 115, 44);
  animation-name: clicked;
  animation-duration: 0.6s;
  animation-iteration-count: infinite;
  animation-direction: alternate ;
}
button {
  /* margin-top: 100%; */
  width: 50%;
}
.Content2{
  width: 70%;
  height: 30vh;
  margin: 5% 0;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
 
}
.Content2 button{
  width: 20%;
  height: 50px;
}